import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController, Tabs } from 'ionic-angular';
import { AppSoundProvider } from '../../providers/app-sound/app-sound';
@IonicPage()
@Component({
  selector: 'page-recent-draw',
  templateUrl: 'recent-draw.html'
})
export class RecentDrawPage {

  constructor(public navCtrl: NavController, public navParams: NavParams, public viewCtrl: ViewController, public appSound: AppSoundProvider) { }

  ionViewDidLoad() {
    console.log('ionViewDidLoad RecentDrawPage');
  }
  ionViewWillEnter() {
    this.viewCtrl.showBackButton(false);
  }
  close() {
    this.appSound.play('buttonClick');
    this.navCtrl.pop();
  }
  viewMore() {
    this.navCtrl.popToRoot().then(() => {
    
    })
  }
}



